import React from 'react';

import { Container } from 'reactstrap';


const TermsUsePage = ({ date_update = '29 de junho de 2018' }) => (
  <div className="l-site-masteraula__public-home c-terms-use middle-box">
    <Container>
      <h3>
        <i className="fa fa-thumbs-up" />
        {' '}
Damos boas-vindas ao Portal MasterAula.
      </h3>

      <div className="c-terms-use__section">
        <p>
O Portal MasterAula  mantido pela MUPI e nossa misso  apoiar o trabalho de educadores atravs da oferta de formao continuada online e de contedos que acreditamos serem vlidos para esses fins.
        </p>
        <p>
Ao usar nosso Portal, voc est concordando com estes termos e com nossa poltica de privacidade. Leia-os com ateno. Este documento contm INFORMAES LEGAIS importantes e indispensveis  utilizao do Portal.
        </p>
      </div>

      <div className="c-terms-use__section">
        <h5>
SOBRE O CONTEDO DO PORTAL E O ACESSO AO MESMO
        </h5>
        <p>
Os contedos e as informaes contidas neste site podero ser atualizadas ou modificadas periodicamente. Por conseguinte, no devem ser interpretadas como definitivas.
        </p>
      </div>

      <div className="c-terms-use__section">
        <h5>
SOBRE A CONTA E O CADASTRO DO USURIO
        </h5>
        <p>
Para utilizar o Portal voc precisa criar uma Conta pessoal e intransfervel atravs de endereo de email vlido e de sua posse e de senha pessoal e confidencial. Caso tenha conhecimento de qualquer uso no autorizado de sua senha ou conta, o usurio dever imediatamente entrar em contato atravs do canal contato@mupi.me.
        </p>
      </div>

      <div className="c-terms-use__section">
        <h5>
SOBRE LINKS EXTERNOS
        </h5>
        <p>
A MUPI no se responsabiliza pela navegao dos USURIOS nos links externos contidos nos contedos dos cursos e tampouco pela disponibilidade dos mesmos, uma vez que a equipe da MUPI no  responsvel pela manuteno nem pelo contedo de tais sites.
        </p>
      </div>

      <div className="c-terms-use__section">
        <h5>
SOBRE CANCELAMENTO
        </h5>
        <p>
A MUPI no efetua devoluo de pagamento aps o USURIO ter realizado o mesmo, exceto quando a oferta do produto adquirido seja cancelada pela MUPI.
        </p>
      </div>

      <div className="c-terms-use__section">
        <h5>
SOBRE OS CONTEDOS PRODUZIDOS PELOS USURIOS
        </h5>
        <p>
A MUPI poder fazer uso, em qualquer um de seus produtos, de todo contedo produzido pelos INSCRITOS durante os cursos oferecidos, como em atividades, trabalhos e comentrios nos espaos de discusso.
        </p>
      </div>

      <div className="c-terms-use__section">
        <h5>
SOBRE PROPRIEDADE INTELECTUAL
        </h5>
        <p>
O uso comercial da expresso Portal como marca, nome empresarial ou nome de domnio, bem como os contedos das telas do Portal, assim como os programas, bancos de dados, redes, arquivos que permitem que o USURIO acesse e use sua conta so de propriedade da empresa responsvel pelo Portal.
        </p>
      </div>

      <p className="date-update-terms">
Atualizado em:
        {date_update}
      </p>
    </Container>
  </div>
);
export default TermsUsePage;



